import type { PanoramaFactory } from '@wix/panorama-client-node';
import {
  createGlobalConfig,
  panoramaClientFactory,
  PanoramaPlatform,
  nodeReporter,
} from '@wix/panorama-client-node';
import { readUserConfig } from '@wix/cli-user-config';
import { getTestOverrides } from '@wix/cli-test-overrides';

// https://dev.wix.com/docs/fed-guild/articles/infra/panorama/client/for-standalone-projects#factory-initialization
const globalConfig = createGlobalConfig();

const overrides = getTestOverrides();
if (overrides.panoramaReporterUrl) {
  globalConfig.setReporter(
    nodeReporter(overrides.panoramaReporterUrl, { silent: true })
  );
}

export type Panorama = PanoramaFactory;

interface PanoramaOptions {
  version: string;
  artifact: {
    groupId: string;
    artifactId: string;
  };
  userId?: string;
}

export async function createPanorama(options: PanoramaOptions) {
  const { version, artifact, userId } = options;

  const userConfig = await readUserConfig();

  const factory = panoramaClientFactory({
    baseParams: {
      platform: PanoramaPlatform.Standalone,
      fullArtifactId: `${artifact.groupId}.${artifact.artifactId}`,
      componentId: 'app', // This property is mandatory, there is validation on server side
      artifactVersion: version,
      uuid: userId,
    },
    reporterOptions: {
      silent: true,
    },
    data: {
      trackingId: process.env.TRACKING_ID,
    },
  })
    .withGlobalConfig(globalConfig)
    .setMuted(!userConfig.telemetry);

  return factory;
}
